<p><span class="rvts58">恶棍学派（</span><span class="rvts59">Scoundrel</span><span class="rvts58">）：</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber302.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">恶棍学派技能一共有</span><span class="rvts20">15</span><span class="rvts21">个。所有的恶棍学派技能都是基于角色的灵巧（</span><span class="rvts20">Finess</span><span class="rvts21">）属性值。恶棍学派技能提供高爆发穿刺性伤害和独特的隐形与偷袭效果。</span>
<br><span class="rvts21">通常情况下，恶棍学派技能主要为角色和队伍提供额外的属性加成和对敌人施加各种减益效果。恶棍派系技能一般需要装备匕首才能来达到最好的效果。</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">恶棍学派技能书可以在以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到：</span>
<br><span class="rvts20">1. </span><span class="rvts21">海尔德（</span><span class="rvts20">Hilde</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">艾特斯特（</span><span class="rvts20">Exter</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">3. </span><span class="rvts21">鞭策爸爸（</span><span class="rvts20">Papa Thrash</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">4. </span><span class="rvts21">精灵大树守卫（</span><span class="rvts20">Elf Treewarder</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸</span>
<br><span class="rvts20">5. </span><span class="rvts21">黑环军需官（</span><span class="rvts20">Black Ring Quartermaster</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">6. </span><span class="rvts21">女祭司杜娜（</span><span class="rvts20">Priestess Duna</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">7. </span><span class="rvts21">胜利者沃斯（</span><span class="rvts20">Victor Voss</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
